.main-navigation__icon--close {
  background-image: url("Icon-Close.31bb77d6.svg");
}

.main-navigation__icon--close--label:not(:is(:focus, :active)) {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  white-space: nowrap;
  width: 1px;
  height: 1px;
  position: absolute;
  overflow: hidden;
}

.main-navigation__icon--phone {
  background-image: url("Icon-Phone-call.4aeee77e.svg");
  position: relative;
}

.main-navigation__icon--phone--label {
  white-space: nowrap;
  color: #1b2129;
  letter-spacing: 1px;
  opacity: 0;
  font-family: Manrope, Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
  transition: opacity .5s ease-in-out, transform .5s ease-in-out;
  display: inline;
  position: absolute;
  top: 50%;
  right: calc(100% + 8px);
  transform: translateY(-50%)translateX(10px);
}

.main-navigation__icon--phone:hover .main-navigation__icon--phone--label {
  opacity: 1;
  transform: translateY(-50%)translateX(0);
}

.main-navigation__icon--phone:not(:hover) .main-navigation__icon--phone--label {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  white-space: nowrap;
  width: 1px;
  height: 1px;
  position: absolute;
  overflow: hidden;
}

.main-navigation__icon--burger {
  position: relative;
}

.main-navigation__icon--burger--label:not(:is(:focus, :active)) {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  white-space: nowrap;
  width: 1px;
  height: 1px;
  position: absolute;
  overflow: hidden;
}

.main-navigation__icon--burger:before, .main-navigation__icon--burger:after {
  content: "";
  background-size: cover;
  width: 24px;
  height: 24px;
  transition: clip-path .5s ease-in-out;
  display: inline-block;
}

@media (width >= 744px) {
  .main-navigation__icon--burger:before, .main-navigation__icon--burger:after {
    width: 32px;
    height: 32px;
  }
}

.main-navigation__icon--burger:before {
  z-index: 1;
  clip-path: inset(0);
  background-image: url("Icon-Burger-menu.efafef00.svg");
}

.main-navigation__icon--burger:after {
  clip-path: inset(0);
  background-image: url("Icon-Burger-menu-hover.13330e8a.svg");
  position: absolute;
  top: 0;
  left: 0;
}

.main-navigation__icon--burger:hover:before {
  clip-path: inset(0 100% 0 0);
}

.main-navigation__icon--burger:hover:after {
  clip-path: inset(0);
}

